.item {
  position: relative;
  border-radius: 2px;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  &:hover .btn {
    opacity: 1;
  }
}
.image {
  width: 100%;
  height: 260px;
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.btn {
  opacity: 0;
  position: absolute;
  top: 0;
  right: 0;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(10, 175, 175, 0.8);
  color: white;
  border: transparent;
  outline: transparent;
  padding: 4px;
  font-size: 16px;
  &:hover {
    color: red;
  }
}
